/* https://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Global */
:root {
  --pastel-positive: rgb(102, 185, 102);
  --pastel-negative: rgb(191, 89, 89);
  --stat-positive: rgb(0, 255, 0);
  --stat-negative: rgb(244, 0, 0);
  --app-negative-base: #791a1a;
  --app-negative-active: #693c3c;
  --cream: rgb(234, 226, 176);
  color-scheme: dark;
}

html {
  font-size: 16px;
}

body {
  background: linear-gradient(180deg, #585858 0%, #272727 100%);
  color: #aaa;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.8;
}

body * {
  /* Not sure why, but this messes up the canvas render, cutting off the left side by 1-2px */
  /* box-sizing: border-box; */
}

h1 {
  color: #fff;
  font-size: 1.8rem;
}

h2 {
  font-size: 1.4rem;
  margin-bottom: 20px;
  text-align: center;
}

a {
  color: #fff;
}

em {
  font-style: italic;
}

strong {
  font-weight: 600;
}

code {
  background-color: rgba(0, 0, 0, 0.8);
  color: rgba(255, 255, 255, 0.8);
  font-family: consolas;
  padding: 1px;
}

pre {
  background-color: rgba(0, 0, 0, 0.8);
  color: rgba(255, 255, 255, 0.8);
  font-family: consolas;
  padding: 1px;
  white-space: pre-wrap;
}

label {
  cursor: pointer;
}

/* Main */
/* ===========================================================================*/
body {
  align-items: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  margin-top: 40px;
  padding: 20px 40px;
}

main {
  align-items: center;
  align-items: flex-start;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-content: space-between;
  /* Tests for small resolution (ie. my TV) */
  /* max-width: 1263px; */
  /* border: 1px dashed rgba(255, 255, 0, 0.2); */
}

.panel {
  background: #151515;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  margin: 0 10px;
  min-height: 400px;
  padding: 15px;
}

.panel--stats {
  width: 550px;
}

.result {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  min-width: 425px;
}

.render .btn {
  flex-grow: 0;
}

.flex-center {
  align-items: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

/* Ibox Edits */
/* ===========================================================================*/
/* Stuff that's not in the wiki's styles */
.ibox__name {
  word-break: break-word;
}

.ibox__type {
  color: var(--cream);
}

.ibox__icon {
  height: 60px;
  width: 60px;
}

.ibox {
  width: 300px;
  max-width: 400px;
  min-width: 200px;
}

/* Ibox Misc */
/* ===========================================================================*/
.staticon {
  display: inline-block;
}

.staticon img,
.btn--staticon img {
  height: auto;
  vertical-align: middle;
  width: 20px;
}

.btn--staticon {
  margin-bottom: 3px;
}

/* Create */
/* ===========================================================================*/
.create__row {
  margin-bottom: 10px;
}

.create__label-wrap {
  display: flex;
}

.create__label-wrap--nocols {
  display: block;
}

.create__label-wrap--nocols .create__label-text {
  width: 100%;
}

.create__label-text {
  font-weight: 600;
  padding: 5px 10px 10px 10px;
  width: 100px;
}

.create__label-text--empty {
  padding: 0;
}

.create__field select,
.create__field input[type="text"],
.create__field input[type="number"],
.create__field textarea {
  background: #000;
  border-radius: 3px;
  border: 1px solid grey;
  color: #fff;
  font-size: 1rem;
  padding: 10px;
  width: 200px;
}

.create__field textarea {
  font-size: 0.9rem;
}

.create__field input[type="checkbox"] {
  padding: 10px;
  width: 20px;
}

.create__help {
  padding: 0 10px;
}

/* Special Fields */
/* ----------------------------------------------------------------------*/
.create__field--tier select {
  width: 88px;
}

.create__field--width input[type="number"] {
  width: 100px;
}

.create__field--has-checkbox {
  padding: 10px 0;
}

.create__field--stats textarea {
  min-height: 150px;
  max-height: 300px;
  min-width: 300px;
  max-width: 500px;
  width: 100%;
}

.create__field--buttons {
  padding: 0 0 10px 0px;
}

.create__field--buttons button {
  margin-right: 5px;
}

.create__row--buttons {
  margin-top: -5px;
}

.create__field--icon-buttons {
  max-width: 380px;
}

/* Canvas */
/* ===========================================================================*/
#canvas {
  border: 1px dashed #343434;
}

/* Buttons */
/* ===========================================================================*/
.btn {
  border-radius: 4px;
  border: 1px solid #000;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  padding: 5px 10px;
}

.btn:not(.btn--has-bg) {
  background: rgba(255, 255, 255, 0.1);
}

.btn:not(.btn--has-bg):hover {
  background: rgba(255, 255, 255, 0.3);
}

.btn--main {
  font-weight: 600;
  padding: 10px 15px;
}

.btn--has-bg:hover,
.btn--has-bg.btn--active {
  opacity: 0.6;
}

.btn--has-bg:hover {
  opacity: 0.4;
}

.btn--red {
  background-color: #791a1a !important;
  /* @todo: bad */
  color: #fff;
}

.btn--red:hover {
  background-color: #693c3c !important;
  /* @todo: bad */
}

/* Icon Buttons */
/* ===========================================================================*/
.icon-btns {
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  width: 250px;
}

.icon-btn {
  cursor: pointer;
  margin-bottom: 5px;
  margin-right: 3px;
}

.icon-btn:hover {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 3px;
}

.icon-btn--active {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 3px;
}

.icon-btn img {
  display: block;
  width: 32px;
}

/* Saved Items */
/* ===========================================================================*/
.saved-items {
  width: 200px;
}

.saved-item {
  display: flex;
  font-size: 0.9rem;
  justify-content: space-between;
  line-height: 1.5;
  margin-bottom: 5px;
}

.saved-item__main {
  width: 90%;
}

.saved-item__main:hover {
  background: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}

.saved-item__number {
  color: rgba(255, 255, 255, 0.1);
  margin-right: 5px;
}

.saved-item__name {
  color: #fff;
  word-break: break-word;
}

.saved-item__delete {
  background-color: var(--app-negative-base);
  border-radius: 3px;
  height: 20px;
  margin-left: 10px;
  text-align: center;
  width: 10%;
}

.saved-item__delete:hover {
  background-color: var(--app-negative-active);
  cursor: pointer;
}

/* Misc */
/* ===========================================================================*/
.tip {
  font-size: 0.7rem;
}

/* Footer */
/* ===========================================================================*/
footer {
  margin-top: 10px;
  text-align: center;
}

.tips {
  font-size: 0.9rem;
}

.credit {
  font-size: 0.7rem;
  margin-top: 20px;
}

/* Utils */
/* ===========================================================================*/
.text-upper {
  text-transform: uppercase;
}

.text-positive {
  color: var(--stat-positive);
  /* color: var(--pastel-positive) */
}

.text-negative {
  color: var(--stat-negative);
  /* color: var(--pastel-negative); */
}

.text-cream {
  color: var(--cream);
}

.mt-5 {
  margin-top: 5px;
}

/* Item Box */
/* ===========================================================================*/
/*
This stuff is all via the wiki, I did the .ibox code for the StatsCard templates:
https://brotato.wiki.spellsandguns.com/Template:StatsCard
https://brotato.wiki.spellsandguns.com/Template:StatsCardWeapon
https://brotato.wiki.spellsandguns.com/Template:StatsCardWeaponTabs

https://brotato.wiki.spellsandguns.com/MediaWiki:Common.css
https://brotato.wiki.spellsandguns.com/MediaWiki:Common.js
*/
.rounded {
  border-radius: 4px;
}

.colorrarity1 {
  color: #c8c8c8;
}

.colorrarity2 {
  color: #4a9bd1;
}

.colorrarity3 {
  color: #ad5aff;
}

.colorrarity4 {
  color: #ff3b3b;
}

.colorrarity5 {
  color: #f7a22d;
}

.rarity0 {
  border: 1px solid rgba(200, 200, 200, 0.5);
  background: rgba(0, 0, 0, 0.5);
}

.rarity1 {
  border: 1px solid rgba(200, 200, 200, 0.5);
  background: rgba(200, 200, 200, 0.5);
}

.rarity2 {
  border: 1px solid rgba(74, 155, 209, 0.5);
  background: rgba(74, 155, 209, 0.5);
}

.rarity3 {
  border: 1px solid rgba(173, 90, 255, 0.5);
  background: rgba(173, 90, 255, 0.5);
}

.rarity4 {
  border: 1px solid rgba(255, 59, 59, 0.5);
  background: rgba(255, 59, 59, 0.5);
}

.rarity5 {
  border: 1px solid rgba(247, 162, 45, 0.5);
  background: rgba(247, 162, 45, 0.5);
}

.ibox {
  opacity: 1;
  padding: 10px;
  transition: 0.1s;
  color: #fff;
  font-size: 15px;
}

:root {
  --name-size: 1.1em;
  --type-size: 1.1em;
  --text-size: 1em;
}

.ibox__top {
  display: flex;
  margin-bottom: 10px;
}

.ibox__icon {
  background-color: rgba(255, 255, 255, 0.1);
  margin-right: 10px;
  flex-shrink: 0;
}

.ibox__icon img {
  height: 60px;
  width: 60px;
  opacity: 0.2;
}

.ibox__name {
  font-size: var(--name-size);
  font-weight: bold;
  line-height: 1.2;
}

.ibox__top-right {
  width: 100%;
}

.ibox__type {
  font-size: var(--name-type);
}

.ibox__stats {
  font-size: var(--text-size);
  line-height: 1.5;
  white-space: pre-wrap;
}

.ibox__stats ul {
  margin: 0;
}

.ibox__stats li {
  list-style: none;
  margin-bottom: 3px;
}

.ibox--hidden {
  opacity: 0.2;
}

.rarity0dark {
  background: black;
}

.rarity1dark {
  background: #202020;
}

.rarity2dark {
  background: #0f2028;
}

.rarity3dark {
  background: #100a1b;
}

.rarity4dark {
  background: #240909;
}

.rarity5dark {
  background: #1a1808;
}
